.header {
  font-family: Inter, Arial, Helvetica, sans-serif;
  color: white;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
  display: flex;
  justify-content: space-between;
  position: relative;
}

@media screen and (max-width:500px) {
  .header {
    flex-direction: column-reverse;
  }
}
